.tag-list {
  padding: 1rem;
  
  &-title {
    font-size: 1rem;
    margin-bottom: 0.5rem;
  }
}

.tags-plain {
  list-style: none;
  padding: 0;
  margin: 0;
  column-count: 3; /* 自动分3列 */
  column-gap: 1rem;

  li {
    break-inside: avoid;
    margin-bottom: 0.3rem;
  }
}

.tag-link {
  font-size: 1.0rem !important;
  line-height: 1.4;
  color: $color-link;
  text-decoration: none;
  display: inline;

  &:hover {
    text-decoration: none;
  }
}

.tag-count {
  font-size: 1.0rem; /* 比标签小一号 */
}

/* 移动端适配 */
@media (max-width: 768px) {
  .tags-plain {
    column-count: 2;
  }
}