pre {
  overflow-x: auto;
  padding: 15px 15px 10px 15px;
  border: 1px dotted $color-border;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  font-size: 13px;
  font-family: $font-family-mono;
  line-height: 22px;
  position: relative;

  .code-copy-btn {
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    border-radius: 0 2px;
    padding: 0;
    font-family: "JetBrains Mono", monospace;
    font-weight: 800;
    font-size: 0.9em;
    line-height: 1.7;
    color: #fff;
    background-color: #8c8c8c;
    min-width: 60px;
    text-align: center;
    cursor: pointer;
    letter-spacing: 0em;
  }

  .code-copy-btn:hover {
    background-color: #666;
    color: #2bbc8a;
  }

  code {
    display: block;
    padding: 0;
    border: none;
  }
}

code {
  font-family: $font-family-mono;
  padding: 0 5px;
  border: 1px dotted $color-border;
  border-radius: 2px;
  -webkit-border-radius: 2px;
}

.highlight {

  & > div {
    border-radius: 2px;
    -webkit-border-radius: 2px;
  }

  pre {
    border: none;
    background: none;
  }

  table {

    pre {
      margin-top: 0;
    }

    td:first-child {
      pre {
        padding-right: 0;
      }
    }

    td:last-child {
      pre {
        padding-left: 0;
      }
    }
  }
}

/* 暗色模式覆盖规则 */
body.dark-theme {
  // 强制覆盖关键颜色变量
  --color-background: #1d1f21;
  --color-text: #c9cacc;
  --color-link: #c9cacc; // 链接颜色与正文相同
  --color-heading: #e0e0e0; // 标题颜色
  $color-heading: #e0e0e0 !global; // 覆盖 SCSS 变量

  // 应用颜色到具体元素
  color: var(--color-text);
  background-color: var(--color-background);

  // 链接颜色覆盖
  a {
    color: var(--color-link);
    &:hover {
      color: lighten(#c9cacc, 10%);
    }
  }

  // 博客标题（假设是 h1.site-title）
  h1, 
  .site-title { 
    color: var(--color-heading) !important;
  }

  // 导航菜单和文章列表
  .nav-links a,
  .post-list a {
    color: var(--color-text);
  }

  // 覆盖主题原有变量（重要！）
  $color-text: #c9cacc !global;
  $color-link: #c9cacc !global;
}



  /* 夜间模式按钮样式 */
  .theme-btn {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0 2px;
    font-size: 1.2em;
    line-height: 1;
    vertical-align: middle;
  }
  /* 默认只显示月亮图标 */
  .dark-icon { display: none; }
  .light-icon { display: inline; }
  /* 暗色模式时显示太阳图标 */
  body.dark-theme .dark-icon { display: inline; }
  body.dark-theme .light-icon { display: none; }
  /* 调整按钮与导航项的间距 */
  .theme-toggle-container {
    margin-left: 0;
    padding-left: 0;
  }


/* 固定在左上角的主题切换按钮 */
.fixed-theme-btn {
  position: fixed;
  top: 1rem;
  left: 1rem;
  z-index: 9999;
}

/* 移动设备适配 */
@media (max-width: 768px) {
  .fixed-theme-btn {
    top: 0.5rem;
    left: 0.5rem;
  }
}

/* 图标切换逻辑 */
.fixed-theme-btn .dark-icon { display: none; }
.fixed-theme-btn .light-icon { display: inline; }
body.dark-theme .fixed-theme-btn .dark-icon { display: inline; }
body.dark-theme .fixed-theme-btn .light-icon { display: none; }

.content {
  // 保留原有其他样式...
  flex: none !important; // 禁用 flex 收缩
  overflow-wrap: normal; // 防止文本换行影响

  // 特别针对图片链接
  a > img {
    display: inline-block;
    height: 1em; // 与文字高度一致
    width: auto;
    vertical-align: -0.15em; // 微调对齐
    margin: 0 0.1em !important;
  }
}


